import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { RiArrowDropRightLine } from "react-icons/ri";
import storyforum from "../../assets/images/storyforum.png";
import "./StoryForum.scss";

const StoryForum = () => {
  return (
    <section className="story-forum">
      <Container>
        <Row>
          <Col lg={6} md={12} sm={12}>
            <img src={storyforum} alt="storyforum" className="position-relative" />
          </Col>
          <Col lg={6} md={12} sm={12}>
            <div className="story-forum__content d-flex justify-content-center flex-column align-items-start">
              <div className="story-forum__content__title">
                <h3 className="sub-heading">Join Our</h3>
                <h2 className="primary-heading">Story Forum</h2>
              </div>
              <p className="paragraph">
                This is dummy copy. It is not meant to be read. It has been
                placed here solely to demonstrate the look and feel of finished,
                typeset text. Only for show. He who searches for meaning here
                will be sorely.
              </p>
              <button className="btn btn-secondary-icon">
                Click Here
                <RiArrowDropRightLine size={25} />
              </button>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};
export default StoryForum;
