import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import BannerImage from "../../assets/images/BannerImage.png";
import "./HomeBanner.scss";
import { Col, Container, Row } from "react-bootstrap";

const HomeBanner = () => {
  const settings = {
    dots: true,
    number: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    vertical: true,
    arrow: false,
  };
  const slides = [
    {
      subTitle: "welcome to",
      title: "GOLDEN ROOM",
      description:
        "This is dummy copy. It is not meant to be read. It has been placed here solely to demonstrate the look and feel of finished, typeset text. Only for show. He who searches for meaning here will be sorely disappointed. These words are here to protvide the reader.",
      bg: BannerImage,
    },
    {
      subTitle: "welcome to",
      title: "GOLDEN ROOM",
      description:
        "This is dummy copy. It is not meant to be read. It has been placed here solely to demonstrate the look and feel of finished, typeset text. Only for show. He who searches for meaning here will be sorely disappointed. These words are here to protvide the reader.",
      bg: BannerImage,
    },
    {
      subTitle: "welcome to",
      title: "GOLDEN ROOM",
      description:
        "This is dummy copy. It is not meant to be read. It has been placed here solely to demonstrate the look and feel of finished, typeset text. Only for show. He who searches for meaning here will be sorely disappointed. These words are here to protvide the reader.",
      bg: BannerImage,
    },
  ];
  return (
    <div className="home-banner h-100">
      <Slider {...settings}>
        {slides.map((slide, index) => {
          return (
            <div key={`slide-${index}`}>
              <div
                className="banner-slide d-flex justify-content-center align-items-center"
                style={{ backgroundImage: `url(${slide.bg})` }}
              >
                <Container>
                  <Row>
                    <Col
                      lg={12}
                      md={12}
                      sm={12}
                      className="d-flex align-items-center position-relative banner-slide__content"
                    >
                      <div className="slide-content position-relative d-flex flex-column align-items-center">
                        <div className="d-flex">
                          <span className="first"></span>
                          <h4 className="primary-heading text-uppercase m-0 px-3">
                            {slide.subTitle}
                          </h4>
                          <span className="last"></span>
                        </div>
                        <h2 className="primary-heading text-uppercase m-0 ">
                          {slide.title}
                        </h2>
                        <p className="paragraph">{slide.description}</p>
                      </div>
                    </Col>
                  </Row>
                </Container>
              </div>
            </div>
          );
        })}
      </Slider>
    </div>
  );
};

export default HomeBanner;
