import React from "react";
import { FaRegComment } from "react-icons/fa";

import "./StoryCard.scss";
interface Post  {
  img: string
}

const StoryCard = ({img}: Post) => {
  return (
    <div className="story-card">
      <div className="story-card__wrapper">
        <img src={img} alt="Story Image" /> 
        <div className="story-card__content py-3">
          <h2>
            This is dummy copy. It is not meant to be read It has been placed.
          </h2>
          <div className="story-card__content__metadata d-flex align-items-center py-4">
            <h4 className="m-0">
              By <span>Liza Smith</span> - Oct 14, 2021
            </h4>
            &nbsp; &nbsp;
            <FaRegComment size={18} />
            &nbsp; 4
          </div>
          <p className="paragraph">
            This is dummy copy. It is not meant to be read. It has been placed
            here solely...
          </p>
        </div>
      </div>
    </div>
  );
};

export default StoryCard;
