import React, { useState } from "react";
import { Row, Col, Container, Nav, Navbar, NavDropdown } from "react-bootstrap";
import logo from "../../assets/images/Logo.png";
import {
  RiArrowDropRightLine,
  GrClose,
  FaFacebookF,
  RiTwitterFill,
  SiInstagram,
  BiMenu,
  ImUser,
} from "react-icons/all";
import "./header.scss";

const Header = () => {
  const [toggle, setToggle] = useState(false);
  const toggleMenu = () => {
    setToggle(!toggle);
  };
  return (
    <header className="header">
      <section className="header__section--color d-flex align-items-center">
        <Container>
          <Row>
            <Col lg={4} md={4} sm={24}>
              <div className="d-flex justify-content-start header__logo">
                <a href="/">
                  <img src={logo} alt="logo" />
                </a>
              </div>
            </Col>
            <Col lg={8} md={8}>
              <div className="d-flex d-lg-none gap-3 align-items-center justify-content-end">
                <button className="btn-menu-toggle mx-4">
                  <ImUser />
                </button>
                <button className="btn-menu-toggle ml-4" onClick={toggleMenu}>
                  <BiMenu />
                </button>
              </div>
              <div className="d-lg-flex text-white align-items-center justify-content-end d-md-none d-none">
                <div className="header__actions d-flex align-items-center px-3">
                  <h4 className="px-2 mb-0">Email:</h4>
                  <a href="mailto:info@goldenroom.com">info@goldenroom.com</a>
                </div>
                |
                <div className="header__actions d-flex align-items-center">
                  <h4 className="px-2 mb-0">Phone Number:</h4>
                  <a href="tel:000-000-0000">000-000-0000</a>
                </div>
                <div className="header__actions d-flex align-items-center px-4">
                  <button className="btn btn-primary-icon ">
                    Login/Registration
                    <RiArrowDropRightLine size={25} />
                  </button>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      <section className="header__section--height d-flex align-items-center">
        <Container>
          <Row>
            <Col lg={10} md={8} sm={12} className="align-self-center">
              <div
                className={`header__navlinks__wrapper ${
                  toggle ? "opened" : ""
                }`}
              >
                <Navbar className="header__navlinks d-flex justify-content-start justify-content-lg-end flex-column flex-lg-row align-items-end align-item-lg-center">
                  <button
                    className="toggle-menu-btn d-block d-lg-none"
                    onClick={toggleMenu}
                  >
                    <GrClose />
                  </button>
                  <ul className="d-flex justify-content-between align-items-center m-0 p-0 w-50">
                    <li className="active"><Nav.Item>Home</Nav.Item></li>
                    <li className="ropdown-content">
                      <NavDropdown
                        id="nav-dropdown-dark-example"
                        title="Stories"
                        menuVariant="dark"
                      >
                        <NavDropdown.Item href="#action/3.1">
                          Stories 1
                        </NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">
                          Stories 2
                        </NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">
                          Stories 3
                        </NavDropdown.Item>
                      </NavDropdown>
                    </li>

                    <li>
                      <a href="#">Articles/Blogs</a>
                    </li>
                    <li>
                      <a href="#">Forum</a>
                    </li>
                    <li>
                      <a href="#">Contact Us</a>
                    </li>
                  </ul>
                </Navbar>
              </div>
            </Col>
            <Col lg={2} md={4} sm={12} className="d-none d-lg-block">
              <div className="header__socailicons d-flex justify-content-around align-items-center h-100">
                <div className="header__socailicons--icons">
                  <a href="https://facebook.com/">
                    <FaFacebookF size={16} />
                  </a>
                </div>
                <div className="header__socailicons--icons">
                  <a href="https://twitter.com/">
                    <RiTwitterFill size={16} />
                  </a>
                </div>
                <div className="header__socailicons--icons">
                  <a href="https://www.instagram.com/">
                    <SiInstagram size={16} />
                  </a>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
    </header>
  );
};

export default Header;
