import React from "react";
import story1 from "../../assets/images/storyImg1.png";
import "./TopStoryCard.scss";
interface Story{
  img:string
}
const TopStoryCard = ({img}:Story) => {
  return (
    <div className="top-story-card">
      <div className="top-story-card__wrapper d-flex flex-row">
        <img src={img} alt="story1" width="120" height="115" />
        <div className="top-story-card__content d-flex flex-column justify-content-center">
          <h2>This is dummy copy. It is not meant to be read.</h2>
          <div className="top-story-card__content__metadata">
            <h4 className="m-0">
              By <span>Liza Smith</span> Oct 14, 2021
            </h4>
          </div>
        </div>
      </div>
    </div>
  );
};
export default TopStoryCard;