import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { RiArrowDropRightLine } from "react-icons/all";
import "./SearchFrom.scss";

const SearchFrom = () => {
  return (
    <div className="search-from">
      <section className="search-from__section">
        <Container className="w-100 d-flex justify-content-center align-items-center">
          <Row className="w-100">
            <Col lg={12} md={12} sm={12}>
              <div className="d-flex flex-md-row align-items-center flex-column w-100 pb-3">
                <h2>Finding great stories</h2>
                <span></span>
              </div>
              <form>
                <Row>
                  <Col xl={7} lg={5} md={5} sm={12}>
                    <input
                      type="text"
                      placeholder="Enter story name..."
                      className="w-100 mb-2 m-md-0"
                    />
                  </Col>
                  <Col xl={3} lg={4} md={3} sm={12}>
                    <select
                      name="categories"
                      placeholder="categories"
                      className="w-100 mb-2 m-md-0"
                    >
                      <option value="Book">Book</option>
                      <option value="Book">Book</option>
                      <option value="Book">Book</option>
                      <option value="Book">Book</option>
                    </select>
                  </Col>
                  <Col xl={2} lg={3} md={4} sm={12} className="d-block
                  ">
                    <button className="btn btn-secondary-dark-icon w-100" >
                      Search Now
                      <RiArrowDropRightLine size={25} />
                    </button>
                  </Col>
                </Row>
              </form>
            </Col>
          </Row>
        </Container>
      </section>
    </div>
  );
};

export default SearchFrom;
