import React from "react";
import { RiArrowDropRightLine } from "react-icons/ri";
import slide from "../../assets/images/Image2.png";
import slide1 from "../../assets/images/Image3.png";
import slide2 from "../../assets/images/Image4.png";
import "./ArticleCard.scss";

interface Blog{
  blog:string
}
const ArticleCard = ({blog}: Blog) => {
  return (
    <div className="article-card d-flex flex-column align-items-center px-2 m-2">
      <img src={blog} alt="slide-img" className="position-relative" />
      <div className="article-card__metadata d-flex justify-content-center align-items-center">
        <h4 className="m-0">
          By <span>Liza Smith</span> - Oct 14, 2021
        </h4>
      </div>
      <h2>
        This is dummy copy. It is not meant to be read It has been placed.
      </h2>
      <p className="paragraph">
        This is dummy copy. It is not meant to be read. It has been placed here
        solely to demonstrate the look and feel of finished, typeset text. Only
        for show.
      </p>
      <button className="btn btn-primary-icon">
        Read More
        <RiArrowDropRightLine size={25} />
      </button>
    </div>
  );
};

export default ArticleCard;
