import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import BookCard from "../BookCard";
import book1 from "../../assets/images/1.png";
import book2 from "../../assets/images/2.png";
import book3 from "../../assets/images/3.png";
import book4 from "../../assets/images/4.png";
import book from "../../assets/images/Image.png";
import "./BookCarousel.scss";

interface Book{
  bookImg:string,
  title:string,
  name:string
}
interface Books {
  name:string,
  data: Array<Book>;
}

const BookCarousel = ({name,data}:Books) => {
  // const data = [
  //   {
  //     bookImg: book1,
  //     title: "Dummy Title of",
  //     name: "story 1",
  //   },
  //   {
  //     bookImg: book2,
  //     title: "Dummy Title of",
  //     name: "story 1",
  //   },
  //   {
  //     bookImg: book3,
  //     title: "Dummy Title of",
  //     name: "story 1",
  //   },
  //   {
  //     bookImg: book4,
  //     title: "Dummy Title of",
  //     name: "story 1",
  //   },
  //   {
  //     bookImg: book,
  //     title: "Dummy Title of",
  //     name: "story 1",
  //   },
  //   {
  //     bookImg: book4,
  //     title: "Dummy Title of",
  //     name: "story 1",
  //   },
  //   {
  //     bookImg: book,
  //     title: "Dummy Title of",
  //     name: "story 1",
  //   },
  // ];

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4.7,
    slidesToScroll: 1,
    draggable: true,
    autoplay: false,
    autoplaySpeed: 5000,
    horizontal: true,
    vertical: false,
    responsive: [
      {
        breakpoint: 1450,
        settings: {
          slidesToShow: 3.7,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2.7,
          slidesToScroll: 1,
          initialSlide: 1,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <section className="slider-carousel py-5">
      <Container>
        <Row>
          <Col lg={12} md={12} sm={12}>
            <div className="book-carousel pb-4 d-flex flex-column align-items-center">
              <div className="book-carousel__title">
                <h3 className="sub-heading">{name}</h3>
                <h2 className="primary-heading">Categories</h2>
              </div>
              <p className="paragraph">
                This is dummy copy. It is not meant to be read. It has been
                placed here solely to demonstrate the look and feel of finished,
                typeset text. Only for show. He who searches for meaning here
                will be sorely disappointed. These words are here to protvide
                the reader.
              </p>
            </div>
          </Col>
        </Row>
      </Container>
      <Slider {...settings}>
        {data.map((item, index) => (
          <BookCard
            title={item.title}
            subTitle={item.name}
            img={item.bookImg}
          />
        ))}
      </Slider>
    </section>
  );
};
export default BookCarousel;
