import React, { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { RiArrowDropRightLine } from "react-icons/ri";
import menImage from "../../assets/images/get-in-touch.png";
import "./GetInTouch.scss";

const GetInTouch = () => {
  const [message, setMessage] = useState(false);
  const messageHandler = () => {
    setMessage(true);
  };
  return (
    <section className="get-in-touch">
      <Container fluid>
        <Row>
          <Col lg={8} className="bg-img">
            <Container className="h-100">
              <Row className="h-100">
                <Col
                  lg={10}
                  md={10}
                  sm={12}
                  className="d-flex justify-content-center flex-column "
                >
                  <div className="get-in-touch__title">
                    <h3>GET</h3>
                    <h2>IN TOUCH</h2>
                  </div>
                  {!message ? (
                    <form className="needs-validation pt-4" onSubmit={messageHandler}>
                      <Row>
                        <Col lg={6}>
                          <input
                            type="text"
                            name="name"
                            placeholder="Name"
                            className="w-100 form-control"
                            required
                          />
                        </Col>
                        <Col lg={6}>
                          <input
                            type="email"
                            name="email"
                            placeholder="Email"
                            className="w-100 form-control"
                            required
                          />
                        </Col>
                        <Col lg={12}>
                          <textarea
                            placeholder="Message"
                            rows={7}
                            className="w-100 form-control"
                            required
                          />
                        </Col>
                        <Col lg={12} className="pt-5">
                          <button
                            className="btn btn-primary-light-icon"
                            type="submit"
                            // onClick={messageHandler}
                          >
                            Send Message
                            <RiArrowDropRightLine size={25} />
                          </button>
                        </Col>
                      </Row>
                    </form>
                  ) : (
                    <div className="d-flex justify-content-start align-items-center min-height">
                    <h3 className="text-white"> Your has been submitted</h3>
                    </div>
                  )}
                </Col>
              </Row>
            </Container>
          </Col>
          <Col lg={4} className="p-0 col-img">
            <img src={menImage} alt="Men Image" className="w-100" />
          </Col>
        </Row>
      </Container>
    </section>
  );
};
export default GetInTouch;
