import React from "react";
import book from "../../assets/images/Image.png";
import "./BookCard.scss";
interface Card  {
    title: string,
    subTitle: string,
    img: string
}
const BookCard = ({title, subTitle ,img} : Card) => {
  return (

      <div
        className="book-card__image d-flex justify-content-end flex-column align-items-center pb-4" 
        style={{ backgroundImage: `url(${img}` }}
      >
        <h2>{title}</h2>
        <h1>{subTitle}</h1>
      </div>
  );
};

export default BookCard;
