import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import StoryCard from "../StoryCard";
import story1 from "../../assets/images/storyImg.png";
import story2 from "../../assets/images/storyImg1.png";
import story3 from "../../assets/images/storyImg2.png";
import story4 from "../../assets/images/storyImg3.png";
import topStory1 from "../../assets/images/storyImg4.png";
import topStory2 from "../../assets/images/storyImg5.png";
import topStory3 from "../../assets/images/topstory1.png";
import topStory4 from "../../assets/images/topstory2.png";

import TopStoryCard from "../TopStoryCard";
import "./FeatureStories.scss";

const data = [
  {
    img:story1,
  },
  {
    img:story2,
  },
  {
    img:story3,
  },
  {
    img:story4,
  },
];
const data2 = [
  {
    img:topStory1,
  },
  {
    img:topStory2,
  },
  {
    img:topStory3,
  },
  {
    img:topStory4,
  },
  {
    img:topStory1,
  },
  {
    img:topStory2,
  },
  {
    img:topStory3,
  },
  {
    img:topStory4,
  },
  {
    img:topStory1,
  },
  {
    img:topStory2,
  },
  {
    img:topStory3,
  },
  {
    img:topStory4,
  },
];
const FeatureStories = () => {
  return (
    <section className="feature-stories py-5">
      <Container>
        <Row>
          <Col lg={8}>
            <div className="feature-stories__title">
              <h3 className="sub-heading">Featured</h3>
              <h2 className="primary-heading">Stories</h2>
            </div>
            <p className="paragraph py-4">
              This is dummy copy. It is not meant to be read. It has been placed
              here solely to demonstrate the look and feel of finished, typeset
              text. Only for show. He who searches for meaning here will be
              sorely disappointed.
            </p>
            <Row>
              {data.map((item, index) => (
                <Col lg={6} md={6} xs={12}>
                  <StoryCard img={item.img} />
                </Col>
              ))}
            </Row>
          </Col>
          <Col lg={4}>
            <div className="top-stories">
              <div className="feature-stories__title">
                <h3 className="sub-heading">Top</h3>
                <h2 className="primary-heading">Stories</h2>
              </div>
              <div className="top-stories__cards">
                <div className="top-stories__cards__wrapper">
                  {data2.map((item, index) => (
                    <TopStoryCard img={item.img} />
                  ))}
                </div>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default FeatureStories;
